@import './styleDefoult/mixin';
.prices {
  background-color: #ffffff;
  border: 1px solid #d41443;
  // height: 440px;
  width: 100%;
  display: block;
  align-items: center;
  justify-content: center;
}

.prices-heading {
  margin-top: 0;
  margin-bottom: 50px;
  text-align: center;
  text-transform: uppercase;
  font-family: Titan One;
  font-size: 34px;
  font-weight: 400;
  line-height: 1.14;
  letter-spacing: 0.04em;
  color: #d41443;

  @media screen and (min-width: 768px) {
    font-size: 48px;
    line-height: 1.15;
    // margin-bottom: 150px;
  }

  @media screen and (min-width: 1200px) {
    font-size: 58px;
    line-height: 1.14;
    // margin-bottom: 259px;
  }
}

.prices-boxes {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
  margin-bottom: 50px;

  @media screen and (min-width: 768px) {
    justify-content: center;
  }
}

.large {
  display: flex;
  margin-left: 2px;
  width: 165px;
  height: 190px;
  border: 1px solid grey;
  align-items: center;
  justify-content: center;
  text-align: center;
  box-shadow: 4px 4px 4px 4px rgba(0, 0, 0, 0.15);
  border: 4px;

  &:hover,
  &:focus,
  &:touchstart {
    color: $accent-cl;
    fill: $accent-cl;
  }

  
  @media screen and (min-width: 768px) {
    margin-left: 5px;
    width: 175px;
    height: 200px;
  }

  @media screen and (min-width: 1200px) {
    height: 300px;
    width: 275px;
  }
}

.big {
  display: flex;
  margin-top: 60px;
  margin-left: 10px;
  width: 100px;
  height: 80px;
  border: 1px solid grey;
  align-items: center;
  justify-content: center;
  text-align: center;
  box-shadow: 4px 4px 4px 4px rgba(0, 0, 0, 0.15);
  border: 4px;

  @media screen and (min-width: 768px) {
    margin-left: 30px;
    width: 110px;
    height: 90px;
  }

  @media screen and (min-width: 1200px) {
    height: 150px;
    width: 130px;
  }
}

.medium {
  display: flex;
  margin-left: 10px;
  margin-top: 20px;
  width: 70px;
  height: 50px;
  border: 1px solid grey;
  align-items: center;
  justify-content: center;
  text-align: center;
  box-shadow: 4px 4px 4px 4px rgba(0, 0, 0, 0.15);
  border: 4px;

  
  @media screen and (min-width: 768px) {
    margin-left: 20px;
    width: 80px;
    height: 60px;
  }

  @media screen and (min-width: 1200px) {
    width: 100px;
    height: 80px;
  }
}

.small {
  display: flex;
  margin-left: 25px;
  margin-top: 25px;
  width: 40px;
  height: 60px;
  border: 1px solid grey;
  align-items: center;
  justify-content: center;
  text-align: center;
  box-shadow: 4px 4px 4px 4px rgba(0, 0, 0, 0.15);
  border: 4px;

  
  @media screen and (min-width: 768px) {
    margin-left: 60px;
    margin-top: 40px;
    width: 50px;
    height: 70px;
  }

  @media screen and (min-width: 1200px) {
    width: 75px;
    height: 90px;
  }
}

.extra-big {
  display: flex;
  margin-top: 20px;
  margin-left: 40px;
  width: 110px;
  height: 90px;
  border: 1px solid grey;
  align-items: center;
  justify-content: center;
  text-align: center;
  box-shadow: 4px 4px 4px 4px rgba(0, 0, 0, 0.15);
  border: 4px;

  @media screen and (min-width: 768px) {
    width: 120px;
    height: 100px;
  }

  @media screen and (min-width: 1200px) {
    width: 150px;
    height: 130px;
  }
}

.medium-big {
  display: flex;
  margin-top: -10px;
  margin-left: 35px;
  width: 80px;
  height: 60px;
  border: 1px solid grey;
  align-items: center;
  justify-content: center;
  text-align: center;
  box-shadow: 4px 4px 4px 4px rgba(0, 0, 0, 0.15);
  border: 4px;

  @media screen and (min-width: 768px) {
    width: 90px;
    height: 70px;
  }

  @media screen and (min-width: 1200px) {
    width: 110px;
    height: 90px;
  }
}

.box-pr {
  display: flex;
  // max-width: 600px;
  // border: 1px solid red;
  // justify-content: space-around;
}

.item {
  position: relative;
}

.item .box-content {
  display: none;
  position: absolute;
  background-color: rgb(211, 241, 255); /* Фон подсказки */
  padding: 0rem 1.2rem; /* Отступы от края */
  // top: -20%; /* Позиция подсказки */
  text-align: center;
  font-size: 2rem;
  color: #c300ff; /* Цвет текста */
}

.item:hover .box-content {
  display: block;
}
